<script>
import {request} from '@/network/request.js'

  export default {
    name: "Index",
    data() {
      return {
        errorMes:'数据加载异常，请刷新！！',
        show:false,
        menusList:[]
      }
    },
    created(){
        if(localStorage.getItem("token")){
          this.show=true
        request({
          url: '/menuShow',
          method: 'get',
        }).then(res => {
            let data = res.data.message
            let indexArray=[]
             for(let i=0;i<4;i++){
                let index = Math.floor(Math.random()*data.length)
                indexArray.push(index)
                 indexArray=[...new Set(indexArray)]
                 this.menusList.push(data[indexArray[i]])
             }
             console.log(this.menusList)
              
        }).catch(err => {
          console.log(err);
        });
      }else{
        this.show=false
         this.errorMes ='请登录，查看菜品!!!'
      }
    },
    methods: {
      godetails(i){
        this.$router.push({
          path:'/menucontent',
         query:{id:i}
        })
      }
    },

  }
</script>

